	import { required } from '@/services/validators';
	import { useApp } from '@/composables/app.js';
	import { useAddPage } from '@/composables/addpage.js';
	const props = defineProps({
		pageName : {
			type : String,
			default : 'datosprueba',
		},
		routeName : {
			type : String,
			default : 'datospruebaadd',
		},
		apiPath : {
			type : String,
