		},
		submitButtonLabel: {
			type: String,
			default: "Entregar",
		},
		formValidationError: {
			type: String,
			default: "El formulario no es vÃ¡lido",
		},
		formValidationMsg: {
			type: String,
			default: "Por favor complete el formulario",
		},
		msgAfterSave: {
			type: String,
			default: "Grabar agregado exitosamente",
		},
		showHeader: {
			type: Boolean,
			default: true,
		},
		showSubmitButton: {
			type: Boolean,
			default: true,
		},
		redirect: {
			type : Boolean,
			default : true,
		},
		isSubPage: {
			type : Boolean,
			default : false,
		},
		pageData: { // use to set formData default values from another page
			type: Object,
			default: () => {} 
		},
	});
	const app = useApp();
	const formDefaultValues = {
		categoria: "", 
		ciudad: "", 
		estado: "", 
		fabricante: "", 
		fecha_envio: "", 
		fecha_pedido: "", 
		forma_envio: "", 
		id_pedido: "", 
		cliente: "", 
		producto: "", 
		paisregion: "", 
		region: "", 
		segmento: "", 
		subcategoria: "", 
		cantidad: "", 
		descuento: "", 
		ganancia: "", 
		numero_registros: "", 
		relacion_ganancias: "", 
		total: "", 
	};
	const formData = reactive({ ...formDefaultValues });
	//form validation rules
	const rules = computed(() => {
		return {
			categoria: {  },
			ciudad: {  },
			estado: {  },
			fabricante: {  },
			fecha_envio: {  },
			fecha_pedido: {  },
			forma_envio: {  },
			id_pedido: {  },
			cliente: {  },
			producto: {  },
			paisregion: {  },
			region: {  },
